import React from 'react';
import Container from '@material-ui/core/Container';
import OutlinedButons4 from '../Components/DownloadButton2';
import BasicTable2 from '../Components/Table2';
import ArrowBackIosRounded from '@material-ui/icons/ArrowBackIosRounded';
import { Link, Router } from "react-router-dom";
import Paper from '@material-ui/core/Paper';
import { Grid } from '@material-ui/core';


export default function Arrivals7() {
  return (
    <React.Fragment>
      <Container fixed>
        <Paper elevation={0}>
          Statistics
        </Paper>

        <Paper elevation={0}>
          Arrivals by Charter Flights during Season
        </Paper>
        <BasicTable2 />
        <Grid container spacing={1}>
          <Grid item xl={4} xs={4}>
            <Link to={process.env.PUBLIC_URL + '/Etourist3'}>
              <ArrowBackIosRounded />
            </Link>
          </Grid>
          <Grid item xl={4} xs={4}>
            <OutlinedButons4 />
          </Grid>
        </Grid>
      </Container>
    </React.Fragment>
  );
}